.list {
  color: #ffffff;
  margin: auto;
  height: 55px;
  width: 350px;
  background-color: #f5b0cb;
  border-radius: 5px;
}
.list p input:focus {
  outline: none;
}
.list p {
  padding: 10px;
  position: relative;
}

.list p input {
  background-color: transparent;
  border: 0;
  color: #fff;
  font-size: 18px;
}
/* span{
